import React, { useEffect } from 'react'
import Divider from '../Divider'
import ScrollReveal from "scrollreveal"

import "./about.css"

const img = "https://mdbootstrap.com/img/Photos/Avatars/img%20%2810%29.webp"

function About() {

    useEffect(() => {
        ScrollReveal({
            reset: true,
            delay: 100
        }).reveal('.headline');
    }, [])


    return (
        <div className="about headline" id="aboutPage">
            <div className="aboutTitle">
                <h1>
                    About

                </h1>
            </div>
            <Divider width="4%" />
            <br />
            <div className="aboutContent">

                <div className="aboutMe">

                    <div className="aboutDesc">
                        <p className="descriptionAbout">
                            I am student of undergraduating in 2023 from the University of Burdwan with taking stream ➡
                            computer science and engineering
                            with full regards.
                            <hr />

                        </p>
                        <div className="aboutName">
                            <h1>
                                Md Ashif Reza
                            </h1>
                        </div>
                        <div className="btnMore">
                            <button>
                                <a href="https://www.linkedin.com/in/md-ashif-reza-a926b6203/" target="_blank">
                                    know more
                                </a>
                            </button>
                        </div>
                    </div>
                    <div className="aboutImage">
                        <img src="./images/user1.png" alt="" srcset="" />
                    </div>
                </div>

            </div>

        </div >
    )
}

export default About
