import React from 'react'
import "./Works.css"

function WorkHistory() {

    const projectData = [
        {
            title: "Ecomerce website",
            url: "https://github.com/md-786910/shameshoppee"
        },
        {
            title: "Google Clone",
            url: "https://github.com/md-786910/Google-clone"
        },
        {
            title: "Youtube Clone",
            url: "https://github.com/md-786910/youtubeCone"
        },
        {
            title: "Rutine Handlers",
            url: "https://github.com/md-786910/rutine_handler"
        },
        {
            title: "Image download and upload",
            url: "https://github.com/md-786910/upload-image-node"
        },
        {
            title: "My collge website clone",
            url: "https://github.com/md-786910/uit"
        },
        {
            title: "Chat app",
            url: "https://github.com/md-786910/chat-app"
        },
        {
            title: "News Monkey app",
            url: ""
        },
        {
            title: "Paint app",
            url: "https://github.com/md-786910/paint"
        },
    ]

    return (
        <>
            <div className="wrapperWorkHistory">

                <div className="historyTitle">
                    <h3>
                        History 🎯
                    </h3>
                </div>
                <div className="contentHostory">
                    <p>
                        I have start work in development field since <b>August 2019</b>
                        &nbsp;and it is continued till now.
                        i have learn so many <b>languages</b> &nbsp;
                        and <b>Team management</b>&nbsp;
                        and i have done many <b>project</b>
                        <br />
                        <strong>
                            Project be like :( View Code 📳)
                        </strong>
                        <div className="projectDoneHistory">
                            {
                                projectData.map((elem, index) => {
                                    return (
                                        <>
                                            <p>
                                                <a href={elem.url} className="projectLink" target="_blank" attribute="href" title={elem.title}>
                                                    {index + 1} . {elem.title}
                                                </a>
                                            </p>
                                        </>
                                    )
                                })
                            }


                        </div>
                    </p>
                </div>
            </div>
        </>
    )
}

export default WorkHistory
