import React from 'react'
import { Chip, Avatar } from "@mui/material"


import "./Skills.css"

const skillDataFrontend = [
    {
        label: "HTML",
        avatar: "H",
        color: "primary"
    },
    {
        label: "CSS",
        avatar: "C",
        color: "primary"
    },
    {
        label: "JAVASCRIPT",
        avatar: "J",
        color: "primary"
    },
    {
        label: "REACT JS",
        avatar: "R",
        color: "primary"
    },
    {
        label: "BOOTSTRAP",
        avatar: "B",
        color: "primary"
    },
    {
        label: "TAILWIND CSS",
        avatar: "T",
        color: "primary"
    },
    {
        label: "WORDPRESS",
        avatar: "W",
        color: "primary"
    },
]

const skillDataBackend = [
    {
        label: "NODE JS",
        avatar: "N",
        color: "success"
    },
    {
        label: "EXPRESS JS",
        avatar: "E",
        color: "success"
    },
    {
        label: "PHP",
        avatar: "P",
        color: "success"
    },
    {
        label: "MONGO DB",
        avatar: "M",
        color: "success"
    },
    {
        label: "MYSQL",
        avatar: "M",
        color: "success"
    },
    {
        label: "NEXT JS",
        avatar: "N",
        color: "success"
    },
    {
        label: "REDDIS",
        avatar: "R",
        color: "success"
    },
]

const skillDataProgramming = [
    {
        label: "C",
        avatar: "C",
        color: "info"
    },
    {
        label: "C++",
        avatar: "CPP",
        color: "info"
    },
    {
        label: "JAVA",
        avatar: "J",
        color: "info"
    },
    {
        label: "PYTHON",
        avatar: "P",
        color: "info"
    },
]

function Skills() {
    return (
        <>
            <div className="skillWrapper">
                <div className="AllSkill">

                    <div className="topSkill">
                        <h3>
                            FrontEnd Skills 🎩
                        </h3>
                        <div className="keepSkill">
                            {
                                skillDataFrontend.map((elem, index) => {
                                    return (
                                        <div className="chips" key={index}>
                                            <Chip color={elem.color} label={elem.label} avatar={<Avatar>{elem.avatar}</Avatar>} />
                                        </div>
                                    )
                                })
                            }



                        </div>
                    </div>

                    <div className="middleSkill">
                        <h3>
                            BackEnd Skills🅰
                        </h3>

                        <div className="keepSkill">
                            {
                                skillDataBackend.map((elem, index) => {
                                    return (
                                        <div className="chips" key={index}>
                                            <Chip color={elem.color} label={elem.label} avatar={<Avatar>{elem.avatar}</Avatar>} />
                                        </div>
                                    )
                                })
                            }



                        </div>

                    </div>
                    <div className="lowSkill">
                        <h3>
                            Programming Skill ❌
                        </h3>

                        <div className="keepSkill">
                            {
                                skillDataProgramming.map((elem, index) => {
                                    return (
                                        <div className="chips" key={index}>
                                            <Chip color={elem.color} label={elem.label} avatar={<Avatar>{elem.avatar}</Avatar>} />
                                        </div>
                                    )
                                })
                            }
                        </div>

                    </div>

                </div>
            </div>
        </>
    )
}

export default Skills
