import React from 'react'

function Educations() {


    const EducationData = [
        {
            batch: "Ten class school information 🔟",
            school: "Holy Mission Sr Sec School 🏫",
            place: "Muzaffarpur Bihar",
            marks: " 90% 👌",
            passing_year: "2016 - 2017",
        },
        {
            batch: "Tweleve class school information 🕧",
            school: "Levana Public School 🏫",
            place: "Bihar Chakia",
            marks: "72% 👌",
            passing_year: "2017 - 2019",
        },
        {
            batch: "Collge information ❄",
            school: "University Institute of Technology Berdhmann🏫",
            place: "Burdwan West Bengal",
            marks: "ongoing 🔂",
            passing_year: " 2019 - 2023",
        },
    ]



    return (
        <>
            {
                EducationData.map((elem, index) => {
                    return (
                        <div className="wrapper10" key={index}>

                            <div className="details10">
                                <h3>
                                    {elem.batch}
                                </h3>
                            </div>

                            <div className="info10">
                                <p className="schoolName">
                                    {elem.school}
                                </p>
                                <p>
                                    <b>
                                        Place :
                                    </b>
                                    <span>{elem.place}</span>
                                </p>
                                <div className="marks10">
                                    <p>
                                        <b>Marks</b> (%) : <span className="mark10%">
                                            {elem.marks}
                                        </span>
                                    </p>
                                    <p>
                                        <b>Passing Years</b> : <span className="10year">
                                            {elem.passing_year}
                                        </span>
                                    </p>
                                </div>

                            </div>

                        </div>
                    )
                })
            }
        </>
    )
}

export default Educations
