import React, { useState, useEffect } from 'react'
import Divider from '../Divider'
import Alert from '../Alert'

import "./contact.css"
function Contact() {

    const [color, setColor] = useState("")
    const [title, setTitle] = useState("")
    const [message, setMessage] = useState("")

    const [user, setUser] = useState({
        name: "",
        email: "",
        subject: "",
        message: "",
    })

    const handlerChanage = (e) => {

        const name = e.target.name
        const value = e.target.value
        setUser({ ...user, [name]: value })
    }

    const sendData = async (e) => {
        e.preventDefault()
        try {
            const { name, email, subject, message } = user

            if (!name || !email || !subject || !message) {
                alert("Please enter form")
                setTitle("All field required")
                setColor("warning")
                setMessage("Plase fill form Details Kindly")
            }
            else {
                const response = await fetch("http://localhost:5000/user", {
                    method: "POST",
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ name: name, email: email, subject: subject, message: message })
                })
                const data = await response.json()

                setTitle(response.status)
                setColor("success")
                setMessage(data.msg)
            }

        } catch (error) {
            return <Alert status="error" message={error.message} color="danger" />
        }

    }

    return (
        <>
            <div className="contactPage" id="contactPage">
                <div className="contactTitle">
                    <h1>
                        Contact Us
                    </h1>

                    <Alert status={title} color={color} message={message} />

                </div>
                <Divider width="10%" />

                <div className="contactForm">

                    <div className="contactLeft">
                        <div className="getTouch">
                            <h2>
                                Get in touch 👋
                            </h2>

                            <div className="contactDesc">
                                <p>
                                    <i className="fa fa-2x fa-quote-left me-2" style={{ color: '#231d3f' }} ></i>
                                    Hi if you get problems with my portfolio
                                    then contact me with freely.
                                </p>
                            </div>

                            <div className="contactsSign">
                                <div className="contactSign1">
                                    <i className="fa fa-phone"></i>
                                    <span className="spanContactTitle">
                                        Mobile No
                                    </span>
                                    <p>
                                        6201991257
                                    </p>

                                </div>
                                <div className="contactSign1">
                                    <i className="fa fa-user"></i>
                                    <span className="spanContactTitle">
                                        Name
                                    </span>
                                    <p>
                                        Md Ashif Reza
                                    </p>

                                </div>
                                <div className="contactSign1">
                                    <i className="fa fa-map-marker-alt"></i>
                                    <span className="spanContactTitle">
                                        Address
                                    </span>
                                    <p>
                                        Bihar Muzaffarpur
                                    </p>
                                </div>
                                <div className="contactSign1">
                                    <i className="fa fa-envelope"></i>
                                    <span className="spanContactTitle">
                                        Email
                                    </span>
                                    <p>
                                        mdashifreza3@gmail.com
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="contactRight">

                        <div className="contactTitleUs">
                            <h2>
                                💁‍♂️ Contact Us
                            </h2>
                        </div>

                        <form >
                            <div className="contactFormPage">

                                <div className="name_email">

                                    <input type="text" name="name" value={user.name} id="" placeholder="Name" className="emailAndName" onChange={handlerChanage} />

                                    <input type="email" name="email" value={user.email} id="" placeholder="Email" className="emailAndName" onChange={handlerChanage} />

                                </div>

                                <div className="contactSubject">

                                    <input type="text" name="subject" value={user.subject} id="contactSubject1" placeholder="Subject" onChange={handlerChanage} />

                                </div>

                                <div className="contactTextArea">

                                    <textarea name="message" value={user.message} id="" placeholder="message" onChange={handlerChanage} ></textarea>

                                </div>

                                <div className="btnMessage">
                                    <button onClick={sendData}>
                                        message me
                                    </button>
                                </div>

                            </div>
                        </form>

                    </div>
                </div>
            </div>
        </>
    )
}

export default Contact
