import React from 'react'
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';

import { TabContext, TabPanel, TabList } from '@mui/lab';

import Divider from '../Divider'
import "./student.css"
import Educations from './Educations';
import WorkHistory from './WorkHistory';
import Skills from './Skills';
import Interest from './Interest';


function StudentDetails() {
    const [value, setValue] = React.useState('1');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className="studentDetails">
            <div className="studentTitle">
                <h1>
                    Student Information

                </h1>
                <Divider width="17%" />
            </div>

            <div className="student_wrapper">

                <Box sx={{ width: '100%', typography: 'body1' }} className="student_box">
                    <TabContext value={value}>
                        <Box sx={{ borderBottom: 2, borderColor: '#231d3f', subtitle1: 'h2', }}>
                            <TabList onChange={handleChange} aria-label="Tab">

                                <Tab className="tab" label="Educations" value="1" />
                                <Tab className="tab" label="Work History" value="2" />
                                <Tab className="tab" label="Skills" value="3" />
                                <Tab className="tab" label="Interest" value="4" />

                            </TabList>
                        </Box>
                        <TabPanel value="1">
                            <div className="schoolDetails">
                                <Educations />
                            </div>
                        </TabPanel>

                        <TabPanel value="2">

                            <div className="workHistory">
                                <WorkHistory />
                            </div>

                        </TabPanel>
                        <TabPanel value="3">
                            <div className="skills">
                                <Skills />
                            </div>
                        </TabPanel>
                        <TabPanel value="4">
                            <div className="interest">
                                <Interest />
                            </div>
                        </TabPanel>
                    </TabContext>
                </Box>

            </div>
        </div >
    )
}

export default StudentDetails
